import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

export const constantRoutes = [
  {
    path: '/login',
    name: 'Login',
    hidden: true,
    component: () => import('../views/login/index'),
    meta: { title: '登录' }
  },
  {
    path: '/',
    name: 'home',
    component: () => import('../components/Layout/Index'),
    hidden: true,
    children: [
      {
        path: '/article',
        name: 'ArticleList',
        component: () => import('../views/modules/article/list.vue'),
        meta: {
          title: '博客列表',
          icon: 'mdi-calendar-month',
          roles: ['album:view']
        }
      },
      {
        path: '/article/edit',
        name: 'ArticleEdit',
        hidden: true,
        component: () => import('../views/modules/article/edit'),
        meta: {
          title: '新增博客',
          icon: 'mdi-calendar-month',
          roles: ['album:view']
        }
      },
      {
        path: '/article/:id/edit',
        name: 'ArticleEdit',
        hidden: true,
        component: () => import('../views/modules/article/edit'),
        meta: {
          title: '编辑博客',
          icon: 'mdi-calendar-month',
          roles: ['album:edit']
        }
      },
      {
        path: '/article/:id',
        name: 'ArticleView',
        hidden: true,
        component: () => import('../views/modules/article/view'),
        meta: {
          title: '博客详情',
          icon: 'mdi-calendar-month',
          roles: ['album:view']
        }
      },
      {
        path: '/album',
        name: 'Album',
        component: () => import('../views/modules/album/index'),
        meta: {
          title: '相册',
          icon: 'mdi-calendar-month',
          roles: ['album:view']
        }
      }
    ]
  },
  // 404 page must be placed at the end !!!
  {
    path: '*',
    redirect: '/404',
    hidden: true
  }
]

const createRouter = () => new VueRouter({
  // mode: 'history', // require service support
  base: process.env.BASE_URL,
  routes: constantRoutes
})

const router = createRouter()

export default router
