import { login, logout, getInfo } from '@/api/user'
import { getToken, removeToken, setToken } from '@/utils/auth'

const state = {
  id: null,
  token: getToken(),
  authorities: [],
  name: '',
  avatar: '',
  email: ''
}

const mutations = {
  SET_ID: (state, id) => {
    state.id = id
  },
  SET_TOKEN: (state, token) => {
    state.token = token
  },
  SET_INTRODUCTION: (state, introduction) => {
    state.introduction = introduction
  },
  SET_NAME: (state, name) => {
    state.name = name
  },
  SET_EMAIL: (state, name) => {
    state.email = name
  },
  SET_AVATAR: (state, avatar) => {
    state.avatar = avatar
  },
  SET_AUTHORITIES: (state, authorities) => {
    state.authorities = authorities
  }
}

const actions = {
  login({ commit }, userInfo) {
    const { username, password } = userInfo
    return new Promise((resolve, reject) => {
      login({
        username: username.trim(),
        password
      }).then(resp => {
        const token = resp.data.data.token
        if (resp.data.code === 200 && token) {
          commit('SET_TOKEN', token)
          setToken(token)
          resolve()
        } else {
          reject(resp.data.data)
        }
      }).catch(error => {
        reject(error)
      })
    })
  },
  getInfo({ commit, state }) {
    return new Promise((resolve, reject) => {
      getInfo(state.token).then(resp => {
        const { name, avatar, introduction, authorities, email, id } = resp.data
        commit('SET_ID', id)
        commit('SET_NAME', name)
        commit('SET_AVATAR', avatar)
        commit('SET_INTRODUCTION', introduction)
        commit('SET_AUTHORITIES', authorities)
        commit('SET_EMAIL', email)
        resolve({ name, avatar, introduction, authorities, email })
      }).catch(e => {
        console.error(e)
        reject(e)
      })
    })
  },
  clearToken({ commit, state, dispatch }) {
    return new Promise((resolve, reject) => {
      commit('SET_TOKEN', '')
      removeToken()
      resolve()
    })
  },
  logout({ commit, state, dispatch }) {
    return new Promise((resolve, reject) => {
      logout(state.token).then(() => {
        commit('SET_TOKEN', '')
        removeToken()
        resolve()
      })
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
